"use strict";
const arry = [1, 2, 3, 4, 5, 6, 7, 8, 9];
const morphedArr = arry.at(-1);
console.log("morphedArr", morphedArr);
// on click of button show the result
//buttonId = "show"
//divId = "result"
const intialState = {
    arry: [1, 2, 3, 4, 5, 6, 7, 8, 9],
    morphedArr: [],
};
const reducer = (state = intialState, action) => {
    switch (action.type) {
        case "MORPH_ARRAY":
            return {
                ...state,
                morphedArr: action.payload,
            };
        default:
            return state;
    }
};
const btn = document.getElementById("show");
btn.getElementById("show").addEventListener("click", () => {
    const morphedArr = arry.at(-1);
    document.getElementById("result").innerHTML = morphedArr;
});
const answer = document.getElementById("result");
console.log("answer", answer);
answer.innerHTML = "test";
